import "./TranslationButton.css"
import React from "react";

const TranslationButton = ({showChinese, setShowChinese}) => {
    return (
        <button
          className="translationButton"
          onClick={() => setShowChinese(!showChinese)}
        >
          {showChinese
            ? "Change to English/"
            : "Change to Chinese/"}
        </button>
    )
};

export default TranslationButton;